<template>
 <div class="login-container">
    <!-- el-form ,el-form-item-->
  <el-form :model="user" ref="login-form" class="login-form" :rules="formRules">
       <div class="login-head">
         <div class="logo"></div>
       </div>
  <!--
    form
    1.el-from model
    2. prop
    3. el-fromrules

     1.el-formref 
    2.refel-form validate
   -->
  <el-form-item prop="mobile">
    <el-input
      v-model="user.mobile"
      placeholder=""
    ></el-input>
  </el-form-item>
  <el-form-item prop="code">
    <el-input
      v-model="user.code"
      placeholder=""
    ></el-input>
  </el-form-item>
  <el-form-item prop="agree">
    <el-checkbox v-model="user.agree"></el-checkbox>
  </el-form-item>
  <el-form-item>
    <el-button class="login-btn" type="primary" @click="onLogin" :loading="loginLoading"></el-button>
  </el-form-item>
</el-form>

 </div>
</template>

<script>
import { login } from '@/api/user'
export default {
  name: 'LoginIndex',
  components: {},
  props: {},
  data () {
    return {
      user: {
        mobile: '13911111111', // 
        code: '246810', // 
        agree: false // 
      },
      // checked: true,
      loginLoading: false,
      formRules: { // 
        // : []
        mobile: [
          { required: true, message: '', trigger: 'change' },
          { pattern: /^1[3|5|7|8|9]\d{9}$/, message: '', trigger: 'blur' }
        ],
        code: [
          { required: true, message: '', trigger: 'change' },
          { pattern: /^\d{6}$/, message: '', trigger: 'change' }
        ],
        agree: [
          { // 
            //  callback()
            //  callback(new Error(''))
            validator: (rule, value, callback) => {
              if (value) {
                callback()
              } else {
                callback(new Error(''))
              }
            },
            // message: '',
            trigger: 'change'
          }
        ]
      }
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    onLogin () {
    // 
    // ,,
      this.$refs['login-form'].validate(valid => {
        if (!valid) { //  
          return
        }
        //  
        this.login()
      })
    },
    login () {
      // const user = this.user
      //  loading
      this.loginLoading = true

      // 
      // 1.
      // 2.,
      // 3. 
      // :,
      login(this.user).then(res => {
        console.log(res)
        this.$message({
          message: '',
          type: 'success'
        })
        // loading
        this.loginLoading = false

        // 
        //   JSON
        window.localStorage.setItem('user', JSON.stringify(res.data.data))
        //  this.$router.push('/')
        this.$router.push({
          name: 'home'
        })
      }).catch(err => {
        console.log('', err)
        this.$message.error('')
        // loading
        this.loginLoading = false
      })
    }
  }
}
</script>
