.login-container {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom:0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: url('./login_bg.jpg') no-repeat;
    background-size: cover;
    .login-head{
        height: 57px;
        width: 300px;
        .logo {
            width: 200px;
            height: 57px;
            background: url('./logo_index.png') no-repeat;
            background-size: contain;
            margin: auto;
        }

    }
    .login-form {
        background-color: #fff;
        padding: 50px;
        min-width: 300px;
        .login-btn {
            width: 100%;
        }
    }
}
</style>
