<template>
 <div class="settings-contarner">
<el-card class="box-card">
    <!-- --- -->
  <el-breadcrumb separator-class="el-icon-arrow-right">
  <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
  <el-breadcrumb-item></el-breadcrumb-item>
</el-breadcrumb>
    <el-row>
      <el-col :span="15">
          <!-- ------------- -->
        <el-form
        class="form-body"
        ref="form"
        :model="user"
        label-width="80px"
        :rules="settingRules"
        >
        <el-form-item label="">
            {{ user.id }}
        </el-form-item>
        <el-form-item label="">
            {{ user.mobile }}
        </el-form-item>
        <el-form-item label="" prop="name">
            <el-input v-model="user.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="intro">
            <el-input type="textarea" v-model="user.intro"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
            <el-input v-model="user.email"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="onUpload()" :loading="updateProfileLoading"></el-button>
        </el-form-item>
        </el-form>
      </el-col>
    <el-col :offset="2" :span="4">
        <!-- lable for  id  -->
        <label for="file">
          <el-avatar
            shape="square"
            :size="150"
            fit="cover"
            :src="user.photo"
         ></el-avatar>
            <p></p>
        </label>
        <!-- <p @click="$ref.file.click()"></p> -->
        <input
        type="file"
        id="file"
        ref="file"
        hidden
        @change="onFileChange"
        >
    </el-col>
    </el-row>
</el-card>

<!-- opened Dialog  -->
<!--  -->
<el-dialog
  title=""
  :visible.sync="dialogVisible"
  append-to-body
  @opened="onDialogOpened"
  @closed="onDialogClosed"
  >
 <div class="previewImage-wrap">
    <img
    class="previewImage"
    :src="previewImage"
    ref="preview-image"
    >
 </div>
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false"> </el-button>
    <el-button
    type="primary"
    @click="onUpdatePhoto"
    :loading="updatePhotoLoading"
    > </el-button>
  </span>
</el-dialog>
 </div>
</template>

<script>
import {
  getUserProfile,
  updatePhoto,
  putchUserProfile
} from '@/api/user'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import globalBus from '@/utils/global-bus'
export default {
  name: 'SettingsIndex',
  components: {},
  props: {},
  data () {
    return {
      user: {
        email: '',
        id: null,
        intro: '',
        mobile: '',
        photo: ''
      },
      dialogVisible: false, // 
      previewImage: '', // 
      cropper: null, // 
      updatePhotoLoading: false, // loding
      updateProfileLoading: false, // loading
      // 
      settingRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 7, message: ' 1  7 ', trigger: 'blur' }
        ],
        intro: [
          { required: true, message: '', trigger: 'blur' }
        ],
        email: [
          { required: true, message: 'email', trigger: 'change' },
          { pattern: /^\w+@\w+(\.\w+)+$/, message: 'email', trigger: 'change' }
        ]
      }
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadUser()
  },
  mounted () {},
  methods: {
    onUpload (user) {
      // 
      // this.$REfs[user].validate((vaild) => {
      //   if (vaild) {
      //     alert('')
      //   } else {
      //     alert('')
      //     return false
      //   }
      // })
      // loading
      this.updateProfileLoading = true
      const { name, intro, email } = this.user
      putchUserProfile({
        name,
        intro,
        email
      }).then(res => {
        // 
        this.$message({
          type: 'success',
          message: ''
        })
        // loading
        this.updateProfileLoading = false
        //  layout
        globalBus.$emit('userProfile', this.user)
      })
    },

    loadUser () {
      getUserProfile().then(res => {
        // console.log(res)
        this.user = res.data.data
      })
    },
    onFileChange () {
      // 
      const file = this.$refs.file
      const blobData = window.URL.createObjectURL(file.files[0])
      this.previewImage = blobData
      // 
      this.dialogVisible = true
      // 
      this.$refs.file.value = ''
    },
    // 
    onDialogOpened () {
      // ! img! ! 
      // DOM
      const image = this.$refs['preview-image']

      //   
      // .replace    

      // 
      // .replace
      if (this.cropper) {
        this.cropper.replace(this.previewImage)
        return
      }
      this.cropper = new Cropper(image, {
        // aspectRatio 
        aspectRatio: 1,
        // crop
        // viewMode  0 1
        viewMode: 1,
        // dragMode crop move none
        dragMode: 'none',
        // backgound   true
        // background: true,
        // cropBoxResizable 
        cropBoxResizable: false
      })
    },
    //  
    onDialogClosed () {
      //   (.replace)
      // this.cropper.destroy()
    },
    // 
    onUpdatePhoto () {
      // loading
      this.updatePhotoLoading = true
      //  getCroppedCanvas([options])
      this.cropper.getCroppedCanvas().toBlob((file) => {
        // console.log(file) //
        // FormDate
        const fd = new FormData()
        //  fd.append('', )
        fd.append('photo', file)

        //   dataformdata fd
        updatePhoto(fd).then(res => {
          // console.log(res)
          // 
          this.dialogVisible = false
          // 
          // this.user.photo = res.data.data.photo   
          // ??????????????????????????????????????????????????????
          this.user.photo = window.URL.createObjectURL(file)

          // loading
          this.updatePhotoLoading = false

          // 
          this.$message({
            type: 'success',
            message: ''
          })

          //  layout
          globalBus.$emit('userProfile', this.user)
        })
      })
    }
  }
}

</script>
<style scoped lang="less">
.form-body {
  margin: 20px 0;
}
.previewImage-wrap {
  .previewImage {
    display: block;
    max-width: 100%;
    height: 200px;
  }
}
</style>
