<template>
 <div class="publish-container">
   <el-card class="box-card">
  <div slot="header" class="clearfix">
    <!--  -->
     <el-breadcrumb separator-class="el-icon-arrow-right">
     <el-breadcrumb-item to="/"></el-breadcrumb-item>
     <el-breadcrumb-item>{{ $route.query.id ? "" :  "" }}</el-breadcrumb-item>
     </el-breadcrumb>
     <!-- / -->
  </div>
    <el-form ref="publish-form" :rules="publishRules" :model="article" label-width="60px">
  <el-form-item label="" prop="title">
    <el-input v-model="article.title"></el-input>
  </el-form-item>
  <el-form-item label="" prop="content">
    <el-tiptap
      v-model="article.content"
      :extensions="extensions"
      placeholder=""
      height="400"
    />
  </el-form-item>
  <el-form-item label="">
    <el-radio-group v-model="article.cover.type">
      <el-radio :label="1"></el-radio>
      <el-radio :label="3"></el-radio>
      <el-radio :label="0"></el-radio>
      <el-radio :label="-1"></el-radio>
    </el-radio-group>
    <!-- -------------------------------- -->
    <!--  type>0  -->
    <template v-if="article.cover.type > 0">
      <cover-upload
      v-for="(cover, index) in article.cover.type"
      :key="cover"
      v-model="article.cover.images[index]"
      />

      <!-- <cover-upload
      v-for="(cover, index) in article.cover.type"
      :key="cover"
      @cover-image="onshowImage(index, $event)"
      :upload-image="article.cover.images[index]"
      /> -->
    </template>
  </el-form-item>
  <el-form-item label="" prop="channel_id">
    <el-select v-model="article.channel_id" placeholder="">
      <el-option
       v-for="(channel, index) in channels" :key="index"
       :label="channel.name" :value="channel.id"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onPublish(false)"></el-button>
    <el-button @click="onPublish(true)"></el-button>
  </el-form-item>
</el-form>
</el-card>
 </div>
</template>

<script>
import CoverUpload from './comment/cover-upload'
import {
  getArticleChannels,
  publishArticle,
  getArticle,
  updatedArticle
} from '@/api/article'
// 
import { uploadImage } from '@/api/image'
// 
import {
  ElementTiptap,
  //  extensions
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  Underline,
  Italic,
  Strike,
  ListItem,
  BulletList,
  OrderedList,
  Preview,
  Image,
  TodoItem,
  TodoList, //  TodoItem
  TextColor,
  Blockquote, // 
  TextAlign, // 
  Fullscreen
} from 'element-tiptap'
// import element-tiptap 
import 'element-tiptap/lib/index.css'
export default {
  name: 'PublishIndex',
  components: {
    'el-tiptap': ElementTiptap,
    CoverUpload
  },
  props: {},
  data () {
    return {
      channels: [], // 
      article: {
        title: '', // 
        content: '', // 
        // 
        cover: {
          type: 1,
          images: []
        },
        channel_id: null // id
      },
      //  extensions
      // 
      extensions: [
        new Doc(), // 
        new Text(), // 
        new Paragraph(), //  
        new Heading({ level: 5 }), //  level:5 5
        new Bold({ bubble: true }), // 
        new Underline(), // 
        new Italic(), // 
        new TextColor(), // 
        new Image({
          // base64
          uploadRequest (file) {
            //  FormDate
            const fd = new FormData()
            // image
            fd.append('image', file)
            // return Promise<url>
            return uploadImage(fd).then(res => {
              // returnurl
              return res.data.data.url
            })
          }
        }),
        new TextAlign(), // 
        new Strike(), // 
        new ListItem(), //  
        new BulletList(), // 
        new OrderedList(), // 
        new TodoItem(),
        new TodoList(),
        new Blockquote(), // 
        new Fullscreen(), // 
        new Preview() // 
      ],
      // 
      publishRules: {
        // 
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 30, message: ' 5  30 ', trigger: 'blur' }
        ],
        // 
        content: [
          { required: true, message: '', trigger: 'blur' },
          {
            validate: (rule, value, callback) => {
              if (value === '<p></p>') {
                callback(new Error(''))
              } else {
                callback()
              }
            }
          }
        ],
        // 
        channel_id: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadChannels()
    // ,id id
    if (this.$route.query.id) {
    // 
      this.loadArticle()
    }
  },
  mounted () {},
  methods: {
    // 
    loadChannels () {
      getArticleChannels().then(res => {
        this.channels = res.data.data.channels
      })
    },
    // 
    onPublish (draft = false) {
      // 
      this.$refs['publish-form'].validate(valid => {
        // 
        // console.log(valid) false/true
        if (!valid) {
          return
        }
        //  
        const articleId = this.$route.query.id
        if (articleId) {
          // id id 
          updatedArticle(articleId, this.article, draft).then(res => {
            console.log(res)
            this.$message({
              type: 'success',
              message: '!'
            })
            // 
            this.$router.push('/article')
          })
        } else {
          //  
          publishArticle(this.article, draft).then(res => {
          // console.log(res)
            this.$message({
              type: 'success',
              message: '!'
            })
            // 
            this.$router.push('/article')
          })
        }
      })
    },
    // 
    loadArticle () {
    //  
    //  this.$route
      getArticle(this.$route.query.id).then(res => {
        // console.log(res)
        // console.log(this.$route) // 
        // console.log(this.$route.query) // {id: "1253999219967000576"}
        // console.log(this.$route.query.id) // 1253999219967000576
        this.article = res.data.data
      })
    },
    onshowImage (index, url) {
      // console.log(url)
      this.article.cover.images[index] = url
    }
  }
}
</script>
<style scoped lang="less">
</style>
