<template>
 <div class="cover-upload">
     <!--  -->
     <div class="cover-wrap" @click="showCover">
        <!-- img -->
        <img
        class="cover-image"
        ref="cover-image"
        :src="value">
     </div>
     <el-dialog
        title=""
        :visible.sync="dialogVisible"
        append-to-body
        >
          <el-tabs v-model="activeName" type="card" >
            <el-tab-pane label="" name="first">
              <!--  -->
              <image-list
              :is-show-add="false"
              :is-show-collect="false"
              ref="image-list"
              is-show-select
              />
            </el-tab-pane>
            <el-tab-pane label="" name="second">
                <input type="file" ref="file" @change="onFileChange">
                <img
                width="200"
                ref="preview-image">
            </el-tab-pane>
          </el-tabs>
        <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="onCoverConfirm"> </el-button>
        </span>
    </el-dialog>
 </div>
</template>

<script>
import ImageList from '@/views/image/component/image-list'
import { uploadImage } from '@/api/image'
// import imageList from '../../image/component/image-list.vue'
export default {
  name: 'CoverIndex',
  components: {
    ImageList
  },
  props: ['value'],
  // props: ['upload-image'],
  data () {
    return {
      dialogVisible: false,
      activeName: 'second' //  second
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    showCover () {
      this.dialogVisible = true
    },
    // 
    onFileChange () {
      // console.log(1111)
      //   console.log(this.$refs.file) // input
      //   console.log(this.$refs.file.files) // FileList{0: File, length: 1}
      //   console.log(this.$refs.file.files[0])  // file
      // 
      const file = this.$refs.file.files[0]
      // 
      const blob = window.URL.createObjectURL(file)
      // console.log(blob)
      this.$refs['preview-image'].src = blob
    },
    // 
    onCoverConfirm  () {
      //   activeName  second
      if (this.activeName === 'second') {
        // file 
        const file = this.$refs.file.files[0]
        if (!file) {
          this.$message('')
          return
        }
        //  
        // 
        const fd = new FormData()
        fd.append('image', file)
        // 
        uploadImage(fd).then(res => {
          //   
          this.dialogVisible = false
          // 
          this.$refs['cover-image'].src = res.data.data.url

          // 
          this.$emit('input', res.data.data.url)
        })
      } else if (this.activeName === 'first') {
        //   
        // ref   ()
        const ImageList = this.$refs['image-list']
        // 
        const select = ImageList.select
        // 
        if (select === null) {
          this.$message = ''
          return
        }
        //  
        this.dialogVisible = false
        //  url
        this.$emit('input', ImageList.imageList[select].url)
      }
    }
  }
}
</script>
<style scoped lang="less">
.cover-upload {
    display: flex;
.cover-wrap {
    width: 200px;
    height: 200px;
    border: 1px dashed #ccc;
    .cover-image {
      width: 100%;
      height: 200px;
    }
}
}

</style>
