<el-table
    :data="articles"
    stripe
    class="list-table"
    size="mini"
    style="width: 100%"
    v-loading="loading">
    <el-table-column
      prop="date"
      label="">
    <template slot-scope="scope">
      <el-image
      style="width: 100px; height: 100px"
      :src="scope.row.cover.images[0]"
      fit="cover"
      lazy
      >
      <div slot="placeholder" class="image-slot">
        <span class="dot">...</span>
      </div></el-image>
