    <el-table-column
      label=""
      >
      <!--  -->
      <!-- ,templateslot-scope="scope" -->
      <template slot-scope="scope">
        <el-tag :type="articleStatus[scope.row.status].type">{{ articleStatus[scope.row.status].text }}</el-tag>
      </template>
    </el-table-column>
    <el-table-column
      prop="pubdate"
