 </div>
</template>

<script>
import {
  getArticles,
  getArticleChannels,
  delArticle
} from '@/api/article'
export default {
  name: 'ArticleIndex',
  components: {},
  props: {},
  data () {
    return {
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      articles: [], // 
      articleStatus: [
        { status: 0, text: '', type: 'info' },
        { status: 1, text: '', type: '' },
        { status: 2, text: '', type: 'success' },
        { status: 3, text: '', type: 'warning' },
        { status: 4, text: '', type: 'danger' }
      ],
      tocalCount: 0, // 
      pageSize: 10, // 
      status: null, //  
      channels: [], // 
      channelId: null, // 
      queryDate: null, // 
      loading: true, // true
      page: 1
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadArticles(1)
    this.loadChannels()
  },
  mounted () {},
  methods: {
    // 
    loadArticles (page = 1) {
      this.loading = true
      getArticles({
        page,
        per_page: this.pageSize,
        status: this.status,
        channel_id: this.channelId,
        //   null
        begin_pubdate: this.queryDate ? this.queryDate[0] : null,
        end_pubdate: this.queryDate ? this.queryDate[1] : null
      }).then(res => {
        // this.articles = res.data.data.results
        const { results, total_count: tocalCount } = res.data.data
        this.articles = results
        this.tocalCount = tocalCount
        this.loading = false
      })
    },
    onSubmit () {
      console.log('submit!!')
    },
    //  page ()
    onCurrentChange (page) {
      this.loadArticles(page)
    },
    // 
    loadChannels () {
      getArticleChannels().then(res => {
        this.channels = res.data.data.channels
      })
    },
    // 
    onDelArticle (articleId) {
      this.$confirm('', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        delArticle(articleId.toString()).then(res => {
          // console.log(res)
          // 
          this.loadArticles(this.page)
        })
        this.$message({
          type: 'success',
          message: ''
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  }
}
</script>
